@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: "Bord";
  src: url("./fonts/bord-demo/Bord.otf") format("truetype");
  font-weight: bold;
}
@font-face {
  font-family: "Segoe";
  src: url("./fonts/SegoeUI-VF/Segoe-UI-Variable-Static-Text-Semilight.ttf")
    format("truetype");
  font-weight: 500;
}
@font-face {
  font-family: "Segoe-semi-bold";
  src: url("./fonts/SegoeUI-VF/Segoe-UI-Variable-Static-Display-Semibold.ttf")
    format("truetype");
  font-weight: 600;
}

body {
  @apply leading-[normal] m-0;
}
*,
::before,
::after {
  border-width: 0;
}

.table {
  font-size: 24px;
  width: 100%;
}

.table-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  border-bottom: 2px solid white;
}

.table-row .second {
  display: flex;
  width: 450px;
  justify-content: space-between;
}

.table-row .second h1 {
  width: 129px;
}

a {
  text-decoration: none;
  color: white;
}

.Segoe-semi-bold {
  font-weight: 600;
  font-family: "Segoe-semi-bold";
}

.Segoe {
  font-weight: 500;
  font-family: "Segoe";
}

.Bord {
  font-family: "Bord";
}

@media screen and (max-width: 1024px) {
  .table-row .second {
    width: 300px;
  }
}

@media screen and (max-width: 832px) {
  .table {
    font-size: 18px;
  }
  .table-row .second {
    width: 200px;
  }
}
@media screen and (max-width: 380px) {
  .table {
    font-size: 14px;
  }
  .table-row .second {
    width: 150px;
  }
}
